<template>
  <div id="">
    test2部分{{test1}}
  </div>
</template>

<script>
export default {
  name: 'test2',
  created () {
    this.$parent.$root.$on('eventName', this.eventHandle)
  },
  methods: {
    eventHandle (val) {
      this.test1 = val
    }
  },
  data () {
    return {
      test1: ''
    }
  }
}
</script>



