<template>
  <div id="">
    <input type="text" v-model="imgUrl" />
    {{imgUrl}}
  </div>
</template>

<script>
export default {
  name: 'test1',
  methods: {
    send () {
      this.$parent.$root.$emit('eventName', this.imgUrl)
    }
  },
  watch: {
    imgUrl (val) {
      this.$parent.$root.$emit('eventName', val)
    }
  },
  mounted () {
    this.send()
  },
  data () {
    return {
      imgUrl: 'test1内容'
    }
  }
}
</script>



