<template>
  <div id="">
    <ul class="order-tab">
      <li v-for="(type, index) in typeList" :class="{active: isFoc==index}" @click="tab(type, index)">{{type.name}}</li>
    </ul>
    <div class="order-list">
      <orderInfo v-on:del="delOrder" v-on:cal="calOrder" v-on:sure="sureOrder" :list="filterList"></orderInfo>
    </div>
    <bar></bar>
    <footer class="seat"></footer>
    <loading v-if="isLoading"></loading>
    <confirm v-show="showCfm1">
      <h2 class="confirm-head" slot="head">提示</h2>
      <div slot="content">是否删除订单？</div>
      <p slot="cancel" @click="showCfm1 = false">否</p>
      <p @click="del" slot="sure">删除</p>
    </confirm>
    <confirm v-show="showCfm2">
      <h2 class="confirm-head" slot="head">提示</h2>
      <div slot="content">是否取消订单？</div>
      <p slot="cancel" @click="showCfm2 = false">否</p>
      <p @click="cal" slot="sure">是</p>
    </confirm>
    <confirm v-show="showCfm3">
      <h2 class="confirm-head" slot="head">提示</h2>
      <div slot="content">是否确认收货？</div>
      <p slot="cancel" @click="showCfm3 = false">否</p>
      <p @click="sure" slot="sure">是</p>
    </confirm>
    <alert v-if="showAlt">
      <div slot="content">{{altMessage}}</div>
      <p slot="sure" @click="showAlt = false;">确定</p>
    </alert>
  </div>
</template>

<script>
import axios from 'axios'
import Bar from './components/Bar'
import OrderInfo from './components/OrderInfo'

export default {
  name: 'shoporder',
  components: {
    Bar,
    OrderInfo
  },
  methods: {
    tab: function (_type, _index) {
      this.isFoc = _index
      this.isLoading = true
      this.$http.get('/api/order/tea/list?status=' + _type.status + '&pageSize=200&currentPage=1').then(function (res) {
        this.isLoading = false
        if (res.body.code === 0) {
          this.filterList = res.body.data.results
        } else {
          this.altMessage = res.body.message
          this.showAlt = true
        }
      })
    },
    del: function () {
      this.$http({
        method: 'post',
        url: '/api/order/tea/delete',
        body: {id: this.delId},
        before: function () { this.isLoading = true }
      }).then(function (res) {
        this.isLoading = false
        this.showCfm1 = false
        if (res.body.code === 0) {
          const list = this.filterList
          for (var i = 0; i < list.length; i++) {
            if (list[i].id === this.delId) {
              this.filterList.splice(i, 1)
            }
          }
        } else {
          this.altMessage = res.body.message
          this.showAlt = true
        }
      })
    },
    delOrder: function (_id) {
      this.delId = _id
      this.showCfm1 = true
    },
    cal: function () {
      this.$http({
        method: 'post',
        url: '/api/order/tea/cancel',
        body: {id: this.calId},
        before: function () { this.isLoading = true }
      }).then(function (res) {
        this.isLoading = false
        this.showCfm2 = false
        if (res.body.code === 0) {
          const filterList = this.filterList
          for (var i = 0; i < filterList.length; i++) {
            if (filterList[i].id === this.calId) {
              filterList[i].status = 12
            }
          }
        } else {
          this.altMessage = res.body.message
          this.showAlt = true
        }
      })
    },
    calOrder: function (_id) {
      this.calId = _id
      this.showCfm2 = true
    },
    sureOrder: function (_id) {
      this.sureId = _id
      this.showCfm3 = true
      /* const cf = window.confirm('是否确认收货')
      if (cf === true) {
        this.$http({
          method: 'post',
          url: '/api/order/tea/receipt/confirm',
          body: {id: _id},
          before: function () { this.isLoading = true }
        }).then(function (res) {
          this.isLoading = false
          if (res.body.code === 0) {
            const filterList = this.filterList
            for (var i = 0; i < filterList.length; i++) {
              if (filterList[i].id === _id) {
                filterList[i].status = 4
              }
            }
          } else {
            this.altMessage = res.body.message
            this.showAlt = true
          }
        })
      } */
    },
    sure () {
      this.isLoading = true
      setTimeout(() => function () {
        this.isLoading = false
      }, 500)
    }
  },
  created () {
    document.title = '商城订单'
    axios.get('../static/data.json').then(res => {
      this.isLoading = false
      this.filterList = res.data.orderlist
      /* if (res.body.code === 0) {
        if (res.body.data.results.length > 0) { // 有多条数据
          this.filterList = res.body.data.results
        }
      } else if (res.body.code === 300002 || res.body.code === 300001) {
        window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + this.$root.appId + '&redirect_uri=' + this.$root.enUrl + '&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
      } else {
        this.altMessage = res.body.message
        this.showAlt = true
      } */
    })
  },
  data () {
    return {
      filterList: [],
      typeList: [{name: '全部', status: 0}, {name: '待付款', status: 1}, {name: '待发货', status: 2}, {name: '待收货', status: 3}, {name: '已完成', status: 4}],
      isFoc: 0,
      isLoading: true,
      showCfm1: false,
      showCfm2: false,
      showCfm3: false,
      calId: '',
      delId: '',
      suerId: '',
      showAlt: false,
      altMessage: '错误信息'
    }
  }
}
</script>

<style scoped>
  .order-tab {
    background-color: #fff;
    display: box;
    display: -webkit-box;
    display: -moz-box;
    height: .8rem;
    border-bottom: 0.01rem solid #dedede;
  }
  .order-tab li {
    -webkit-box-flex: 1;
    -moz-box-flex: 1;
    box-flex: 1;
    height: 100%;
    padding: .25rem 0;
    font-size: .24rem;
    text-align: center;
    color: #9b9b9b;
  }
  .order-tab li.active {
    color: #cb4042;
    border-bottom: 1px solid #cb4042;
  }
  .seat {
    min-width: .1rem;
    min-height: 1.1rem;
  }
</style>



